import { Controller } from '@ali/4ever-cangjie';
import { Highlight } from '@ali/4ever-plugin-marks';
import { addMarkAtCurrentRange }  from '@ali/4ever-utils';
import { removeMarkAtCurrentRange }  from '@ali/4ever-utils';


export function setHighlight(controller: Controller, currentColor?: string) {
  if (currentColor && typeof currentColor === 'string') {
    const mark = Highlight.create(currentColor);
    return addMarkAtCurrentRange(controller, mark);
  } else {
    return removeMarkAtCurrentRange(controller, 'highlight');
  }
}
